input:focus,
textarea {
    outline: none !important;
}

input[type=text],
input[type=email],
input[type=tel],
input[type=url],
input[type=search] {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border-radius: 0;
    -moz-border-radius: 0;
    -webkit-border-radius: 0;
}

